.form-item label {
  font-size: 14px;
  font-weight: 600;
}
.form-item {
  display: flex;

  gap: 6px;
  align-items: center;
  flex-wrap: wrap;
}

.form-item input {
  font-size: 16px;
  background-color: rgb(246, 246, 246);
  flex: 1;
  padding: 8px 8px;
  border-radius: 4px;
}
.gender-form-item {
  display: flex;
  gap: 8px;
}

.error-message {
  color: red;
}
